/* From Uiverse.io by kennyotsu */ 
.notifications-container {
    width: 320px;
    height: auto;
    font-size: 0.875rem;
    line-height: 1.25rem;
    display: flex;
    flex-direction: column;
    margin: 30px auto;
    gap: 1rem;
  }
  
  .flex {
    display: flex;
  }
  
  .flex-shrink-0 {
    flex-shrink: 0;
  }
  
  .alert {
    background-color: rgb(254 252 232);
    border-left-width: 4px;
    border-color: rgb(250 204 21);
    border-radius: 0.375rem;
    padding: 1rem;
  }
  
  .alert-svg {
    height: 1.25rem;
    width: 1.25rem;
    color: rgb(250 204 21);
  }
  
  .alert-prompt-wrap {
    margin-left: 0.75rem;
    color: rgb(202 138 4);
  }
  
  .alert-prompt-link {
    font-weight: 500;
    color: rgb(141, 56, 0);
    text-decoration: underline;
  }
  
  .alert-prompt-link:hover {
    color: rgb(202 138 4);
  }